import { ActionTypes } from "../actionTypes/tasks";

type Task = {
  id: number;
  text: string;
  completed: boolean;
};

const initialState: Task[] = [
  {
    id: 1,
    text: "Первая задача",
    completed: true,
  },
  {
    id: 2,
    text: "Вторая задача",
    completed: false,
  },
  {
    id: 3,
    text: "Третья задача",
    completed: true,
  },
  {
    id: 4,
    text: "Четвертая задача",
    completed: true,
  },
];

export function tasksReducer(state = initialState, action: ActionTypes) {
  switch (action.type) {
    case "ADD_TASK":
      return [
        ...state,
        {
          id: state.length > 0 ? state[state.length - 1].id + 1 : 1,
          text: action.inputValue,
          completed: action.completed,
        },
      ];

    case "SET_COMPLETED":
      return state.map((task) =>
        task.id === action.id ? { ...task, completed: !task.completed } : task
      );

    case "DELETE_TASK":
      return state.filter((task) => task.id !== action.id);

    case "MARK_ALL":
      return state.map((task) => ({ ...task, completed: true }));

    case "DELETE_ALL":
      return [];

    case "SUBMIT_EDIT":
      return state.map((task) =>
        task.id === action.currentId
          ? { ...task, text: action.editInput }
          : task
      );
    default:
      return state;
  }
}
