export const addTaskAction = (inputValue: string, completed: boolean) => ({
  type: "ADD_TASK",
  inputValue,
  completed,
});

export const setCompletedAction = (id: number) => ({
  type: "SET_COMPLETED",
  id: id,
});

export const deleteTaskAction = (id: number) => ({
  type: "DELETE_TASK",
  id: id,
});

export const markAllAction = () => ({
  type: "MARK_ALL",
});

export const deleteAllAction = () => ({
  type: "DELETE_ALL",
});

export const submitEditAction = (
  editInput: string,
  currentId: number | undefined
) => ({
  type: "SUBMIT_EDIT",
  editInput,
  currentId,
});
