import { Tab, Tabs } from "@mui/material";
import { useSelector, useDispatch } from "react-redux";
import { changeTabAction } from "../redux/actions/filter";
import { useAppSelector } from "../redux/store";

const filterIndex = ["all", "active", "completed"];

const Filter = () => {
  const dispatch = useDispatch();
  const state = useAppSelector((state) => state.filter.filterBy);

  const handleChangeTab = (_: any, newIndex: number) => {
    const status = filterIndex[newIndex];
    dispatch(changeTabAction(status));
  };

  return (
    <Tabs onChange={handleChangeTab} value={filterIndex.indexOf(state)}>
      <Tab label="Все" />
      <Tab label="Активные" />
      <Tab label="Завершённые" />
    </Tabs>
  );
};

export default Filter;
