import { ChangeTypeActionType } from "../actionTypes/filter";

type FilterType = {
  filterBy: string;
};

const initialState: FilterType = {
  filterBy: "all",
};

export function filterReducer(
  state = initialState,
  action: ChangeTypeActionType
) {
  if (action.type === "CHANGE_TAB") {
    return { filterBy: action.payload };
  }
  return state;
}
