import React from "react";
import { Paper, Divider, Button, List } from "@mui/material";
import { AddField } from "./components/AddField";
import { Item } from "./components/Item";
import InputField from "./components/InputField";
import { useDispatch } from "react-redux";
import axios from "axios";
import Filter from "./components/Filter";
import { useAppSelector } from "./redux/store";
import {
  addTaskAction,
  setCompletedAction,
  deleteTaskAction,
  markAllAction,
  deleteAllAction,
  submitEditAction,
} from "./redux/actions/tasks";


function App() {
  const [inputValue, setInputValue] = React.useState("");
  const [completed, setCompleted] = React.useState(false);
  const [isModalOpen, setIsModalOpen] = React.useState(false);
  const [editInfo, setEditInfo] = React.useState("");
  const [editInput, setEditInput] = React.useState("");
  const [currentId, setCurrentId] = React.useState<number>();

  const dispatch = useDispatch();
  const state = useAppSelector((state) => state);

  React.useEffect(() => {
    const FetchTasks = async () => {
      const { data } = await axios.get(
        "https://61e6a5ccce3a2d0017359303.mockapi.io/tasks"
      );
      console.log(data);
    };
    FetchTasks();
  }, []);

  const addTask = () => {
    dispatch(addTaskAction(inputValue, completed));
    setInputValue("");
    setCompleted(false);
  };

  const changeChecked = (id: number) => {
    dispatch(setCompletedAction(id));
  };

  const deleteTask = (id: number) => {
    dispatch(deleteTaskAction(id));
  };

  const markAll = () => {
    dispatch(markAllAction());
  };

  const deleteAll = () => {
    dispatch(deleteAllAction());
  };

  const handleSubmitEdit = () => {
    dispatch(submitEditAction(editInput, currentId));
    setIsModalOpen(false);
  };

  const handleChangeInput = (event: any) => {
    setInputValue(event.target.value);
  };

  const handleChangeCheckbox = (event: any) => {
    setCompleted(event.target.checked);
  };

  const handleOpenModal = (id: number, text: string) => {
    console.log(id, text, 555);
    if (id === currentId) {
      setIsModalOpen(!isModalOpen);
    } else {
      setCurrentId(id);
      setIsModalOpen(true);
    }
    setEditInfo(`${text}`);
    setEditInput(text);
  };

  const handleEditInput = (event: any) => {
    setEditInput(event.target.value);
  };

  return (
    <div className="App">
      <Paper className="wrapper">
        {isModalOpen && (
          <InputField
            editInput={editInput}
            handleEditInput={handleEditInput}
            editInfo={editInfo}
            handleSubmitEdit={handleSubmitEdit}
          />
        )}
        <Paper className="header" elevation={0}>
          <h4>Список задач</h4>
        </Paper>
        <AddField
          handleChangeCheckbox={handleChangeCheckbox}
          handleChangeInput={handleChangeInput}
          inputValue={inputValue}
          addTask={addTask}
          completed={completed}
        />
        <Divider />
        <Filter />
        <Divider />
        <List>
          {state.tasks
            .filter((obj) => {
              if (state.filter.filterBy === "all") {
                return true;
              }
              if (state.filter.filterBy === "completed") {
                return obj.completed;
              }
              if (state.filter.filterBy === "active") {
                return !obj.completed;
              }
            })
            .map((note) => (
              <Item
                changeChecked={changeChecked}
                key={note.id}
                id={note.id}
                text={note.text}
                completed={note.completed}
                deleteTask={deleteTask}
                handleOpenModal={handleOpenModal}
              />
            ))}
        </List>
        <Divider />
        <div className="check-buttons">
          <Button onClick={markAll}>Отметить всё</Button>
          <Button onClick={deleteAll}>Очистить</Button>
        </div>
      </Paper>
    </div>
  );
}

export default App;
